/* define custom background color and use where appropriate */

#about-me {
  display: flex;
  margin: 10px;
}

.heading {
  display: flex;
  background-color: rgb(58, 57, 57);
  padding: 10px;
}

.section-button {
  margin: 10px;
  padding: 5px;
}

.section-button:hover {
  background-color: aliceblue;
}

.navbar {
  display: flex;
  margin: 10px;
  padding: 10px;
}

.profile {
  height: 300px;
}

.description {
  color: black;
  background-color: gray;
  border: black 2px solid;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
}

.project-image {
  height: 400px;
}

.project-image:hover {
  cursor: pointer;
  border: red 5px solid;
}